<template>
  <el-collapse v-model="activePanels">
    <el-collapse-item name="criteria">
      <template slot="title">
        <i class="el-icon-search" />
        <span class="primary--text">&nbsp; Search Criteria</span>
      </template>
      <SearchCriteria class="mt-1" />
    </el-collapse-item>

    <el-collapse-item name="result">
      <template slot="title">
        <i class="el-icon-check" />
        <span class="primary--text">&nbsp; Search Result</span>
      </template>
      <SearchResult allow-create allow-view class="mt-1" />
    </el-collapse-item>
  </el-collapse>
</template>

<script>
import SearchCriteria from '@/components/RMC/Entity/Search/Criteria'
import SearchResult from '@/components/RMC/Entity/Search/Result'

export default {
  name: 'RMCEntitySearch',

  components: {
    SearchCriteria,
    SearchResult
  },

  data: () => ({
    activePanels: ['criteria', 'result']
  })
}
</script>
<style lang="css" scoped>
>>>.el-collapse-item__header {
  padding-left: 15px;
  padding-right: 15px;
  font-size: 16px;
  /*background-color: #efefef;*/
} 
</style>



